const demo = [{
	avatar:"/static/default.png",
	username:"昵称",
	sex:2, // 0未知 ，1女性，2男性
	age:24,
	isFollow:true
},{
	avatar:"/static/default.png",
	username:"昵称",
	sex:1, // 0未知 ，1女性，2男性
	age:24,
	isFollow:false
}];

import loadMore from '@/components/common/load-more.vue';
import noThing from '@/components/common/no-thing.vue';
import userList from '@/components/user-list/user-list.vue';
export default {
	components:{
		loadMore,
		noThing,
		userList
	},
	data() {
		return {
			// 列表高度
			scrollH:600,
			tabIndex:0,
			tabBars:[{
				name:"互关",
				num:0
			},{
				name:"关注",
				num:2
			},{
				name:"粉丝",
				num:30
			}],
			
			newList:[]
		}
	},
	// 监听点击输入框事件
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search?type=user',
		});
	},
	onNavigationBarButtonTap() {
		uni.navigateBack({
			delta: 1
		});
	},
	onLoad() {
		uni.getSystemInfo({
			success:res=>{
				// px
				this.scrollH = res.windowHeight - uni.upx2px(100);
			}
		})
		// 根据选项生成列表
		this.getData()
	},
	methods: {
		// 获取数据
		getData(){
			var arr = []
			for (let i = 0; i < this.tabBars.length; i++) {
				// 生成列表模板
				let obj = {
					// 1.上拉加载更多  2.加载中... 3.没有更多了
					loadmore:"上拉加载更多",
					list:[]
				}
				if (i < 2) {
					obj.list = demo
				}
				arr.push(obj)
			}
			this.newsList = arr
		},
		// tab切换
		changeTab(index){
			this.tabIndex = index
		},
		// 监听滑动
		onChangeTab(e){
			this.changeTab(e.detail.current)
		},
		// 上拉加载更多
		loadmore(index){
			// 拿到当前列表
			let item = this.newsList[index]
			// 判断是否处于可加载状态（上拉加载更多）
			if (item.loadmore !== '上拉加载更多') return;
			// 修改当前列表加载状态
			item.loadmore = '加载中...'
			// 模拟数据请求
			setTimeout(()=>{
				// 加载数据
				item.list = [...item.list,...item.list]
				// 恢复加载状态
				item.loadmore = '上拉加载更多'
			},10000)
		}
	}
}
